            }]
          }]
        }],
        tableData: []
      }
  },
  methods: {
    test() {

    },
    contextMenuTarget() {

    },
  },
  mounted() {
     let self = this
     this.$axios({
        method: 'get',
        url: 'http://192.168.100.66:8080/api/v2/external/monitor-detail/find-camera-map',
        params: {
        }
      }).then(function(response) {
         let obj = response.data.data
          let arr = []
          Object.keys(obj).forEach(function(key){
             arr.push(obj[key])
          });
          console.log(arr)
          self.tableData = arr
      })
  }
}
</script>

